import {getimgdata} from '../../api/index.js'

const state={//
    imgpagedata:[]
}
const getters={//

}
const mutations={//
    imgpagedata(state,payload){
        // console.log(payload.data)
        state.imgpagedata=payload.data;
    }
}
const actions={//
    imgpage({commit},ids){
        getimgdata(ids).then(res=>{
            commit('imgpagedata',res)
        })
    }
}
export default {
    namespaced:true,
    state,
    getters,
    mutations,
    actions
}


